.contact {
  text-align: center;
  background-color: #2b2d42;
}
.contact-wrap {
  margin: 0 auto;
  padding: 50px 0;
  max-width: 900px;
}
.contact-btn {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-top: 20px;
}
.contact-btn img {
  width: 50px;
  height: 50px;
  margin-right: 5px;
}
.contact-btn a {
  color: rgba(253, 245, 230, 0.6);
}
