/* header */
h1 {
  font-size: 3rem;
}
header {
  position: relative;
  padding-top: 30px;
  text-align: center;
}
nav {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: oldlace;
}
nav a {
  display: inline-block;
  margin: 8px;
  font-weight: 700;
}
/* big screen */
@media (min-width: 440px) {
  nav a {
    margin: 14px;
    font-size: 16px;
  }
}

.content-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  padding: 40px 75px;
  text-align: center;
  max-width: 950px;
  overflow: hidden;
  background-color: rgba(11, 1, 44, 0.9);
  color: oldlace;
}

.content-wrap img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 5px;
}
/* big screen */
@media (min-width: 440px) {
  .content-wrap img {
    width: 200px;
    height: 200px;
  }
}
@media (min-width: 935px) {
  .content-wrap {
    flex-direction: row;
  }
  .content-wrap img {
    width: 250px;
    height: 250px;
  }
  .col-wide {
    padding-left: 50px;
  }
}
/* download button */
.btn {
  background-color: #ef233c;
  color: #fff;
  padding: 0 5px;
}
@media (min-width: 440px) {
  .btn {
    padding: 10px;
  }
}
.download {
  position: absolute;
  bottom: 0px;
  right: 0px;
}
