.education {
  padding: 40px 0;
  background: linear-gradient(
      rgba(141, 153, 174, 0.8),
      rgba(141, 153, 174, 0.6)
    ),
    url(./bg.PNG) no-repeat fixed;
  background-size: cover;
  color: rgb(51, 48, 48);
}
.edu-wrap {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 950px;
}
.edu-wrap h2 {
  font-size: 1.7rem;
  margin-bottom: 30px;
}
.edu-wrap h3 {
  font-size: 1rem;
}
.edu-wrap p {
  /* font-weight: 600; */
  margin-bottom: 10px;
}
.edu-wrap ul {
  margin-top: 0px;
}
.edu-wrap ul li {
  margin-left: 40px;
  list-style-type: circle;
}
.edu-wrap ul li:last-child {
  margin-bottom: 30px;
}
