.experience {
  padding-top: 20px;
  background-color: #edf2f4;
}
.work-wrap {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 950px;
}
.work-wrap h2 {
  font-size: 1.7rem;
}
.job-wraper {
  display: block;
  padding: 30px 0;
}
.job-item {
  width: 100%;
  margin-bottom: 35px;
}
@media (min-width: 935px) {
  .experience {
    padding-top: 40px;
  }
  .job-item {
    display: grid;
    grid-template-columns: 1fr 2.5fr;
    column-gap: 30px;
  }
  .work-wrap h2 {
    font-size: 2rem;
  }
}

.job-name {
  font-size: 1.2rem;
}
.job-description {
  margin-bottom: 10px;
  line-height: 13px;
  font-weight: 500;
}
.key-tech {
  padding-top: 15px;
}
.job-detail {
  line-height: 14px;
  padding-left: 10px;
}
